* {
    margin: 0;
    padding: 0;
}

body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
    text-align: center;
}

.Button {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    width: 34px;
}

.display {
    grid-row: 1/2;
    grid-column: 1/5;
}

.main-text {}

.help-text {
    margin-top: 10px;
    
    font-size: 12px;
}

.main-panel {
    display: grid;
    grid-template-columns: 50px 50px 50px 50px;
    grid-template-rows: 50px 50px 50px 50px 50px 50px;
}

.button15 {
    grid-row: 5/7;
    grid-column: 4/5;
}

.button16 {
    grid-row: 6/7;
    grid-column: 1/3;
}